*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  
}
#root{
  max-width: 1366px;;
}
body{
  overflow-x: hidden;
}
.header{
  width: 100%;
  height: 75px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 0px 7px 0px #ccc;
  padding: 14px;
  background-color: #fff;
}
.header .menu-option{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .search-input{
  display: flex;
  justify-content: center;
  align-items: center;
  border:1px solid #ccc;
}
.header .search-p{
  margin-left:10px;
  margin-top: 5px;
  color: #5f6368;
}
.header .search-input input{
  width: 350px;
  height: 45px;
  padding: 20px;
  font-size: 13px;
  border: none;
  outline: none;
}
.header .setting{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .setting button{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.header button{
  padding: 8px;
  background-color: #fff;
  outline: none;
  border: none;
  color:#5f6368;
  cursor: pointer;
}
.header button:hover{
  background-color: #f2f2f2;
  border-radius: 50%;
}
.open-menu{
  width: 300px;
    height: 80%;
    position: absolute;
    top: 78px;
    left: 0;
    padding: 20px;
    z-index: 2;
    background-color: #fff;
    transform: translateX(-1000px);
    transition: all 0.5s;
    box-shadow: 0 1px 2px 0 rgba(60,64,67,0.302), 0 2px 6px 2px rgba(60,64,67,0.149);
}
.open{
  transform: translateX(-8px);
}
.open-menu div{
  display: flex;
  justify-content: flex-start;
  padding: 10px;
  color: #5f6368;
  align-items: center;
}
.open-menu div a{
  margin-left: 20px;
  text-decoration: none;
  color: #5f6368;
  font-size: 14px;
  cursor: pointer;
}
.setting-option , .View-option{
  width: 160px;
  height:200px;
  position: absolute;
  top:60px;
  right: 50px;
  z-index: 1;
  background-color: #fff;
  border:1px solid #ccc;
  padding: 10px;
  box-shadow: 0 1px 2px 0 rgba(60,64,67,0.302), 0 2px 6px 2px rgba(60,64,67,0.149);
  border-radius: 10px;
  transform: translateY(-500px);
  transition: all 0.5s;
}
.setting-option a , .View-option a{
  font-size: 14px;
  color: #5f6368;
  text-decoration: none;
  line-height: 2.1;
}
.Sopen ,.Vopen{
  transform: translateY(0px);
  transition: all 0.5s;
}
.View-option div{
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.keep{
  color: #5f6368;
  text-decoration: none;
  text-align: center;
  padding: 4px;
  font-size: 22px;
}
.all-notes{
  width: 100%;
  height: 400px;
  display: grid;
  grid-template-columns: repeat(4,1fr);
}
.Notes{
  width:400px;
  height:200px;
  margin: auto;
  margin-top: 20px;
  box-shadow: 1px 2px 5px 0px #ccc;
  border-radius: 10px;
}
.Notes input{
  width: 100%;
  padding:10px;
  outline: none;
  font-size: 15px;
  border: 1px solid #fff;
}
.Notes textarea{
  width: 100%;
  height: 145px;
  padding:10px;
  outline: none;
  font-size: 13px;
  border: 1px solid #fff;
  resize: none;
}
.add{
  margin-top: -4px;
  float: right;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  outline: none;
  border: 1px solid #fff;
  background-color: #fff;
  box-shadow: 4px 4px 4px 0px #ccc;
  cursor: pointer;
  font-weight: bold;
}
.add:hover{
  border:2px solid #ff8c1a;
  box-shadow: 4px 4px 4px 0px #ff8c1a;
}
.plus{
  color: #ff8c1a;
  font-size: 30px;
}

.myNote .content{
  font-size:13px;
  line-height: 1.7;
}
.myNote{
  width: 300px;
  height:auto;
  padding: 20px;
  box-shadow: 1px 2px 5px 0px #ccc;
  border-radius: 20px;
  margin-bottom: 50px;
}
.myNote .title{
  font-size: 17px;
}
.allNotes{
  width: 100%;
  height: auto;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-gap: 30px;
  padding: 10px;
}
.deletebtn{
    background-color: #fff;
    outline: none;
    border: none;
    box-shadow: 4px 4px 4px 0px #ccc;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
}
.deletebtn:hover{
  border:2px solid #ff8c1a;
  box-shadow: 4px 4px 4px 0px #ff8c1a;
}
.delete{
  color: #ff8c1a;
}
.dbtn{
  display: flex;
  justify-content: flex-end;
}
.footer{
    position: fixed;
    bottom: 0;
    width: 100%;
    margin-top: 20px;
    padding: 14px;
    background-color: #fff;
}
.footer p{
  text-align: center;
}

@media(max-width:800px){

  .keep{
    font-size: 14px;
  }
  .klogo{
    width: 20px;
    height: 20px;
  }
  .header{
    padding: 8px;
  }
  .header .search-input input{
    width: 85px;
    height: 15px;
    padding: 0px;
  }
  ..header .search-input{
    height: 28px;
  }
  .Notes{
    width: 330px;
  }
  .allNotes{
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    padding-bottom: 100px;
  }
  .myNote{
    margin-bottom: 50px;
    margin: auto;
  }
  .footer{
    display: flex;
    /* width: 100%; */
    justify-content: space-between;

  }
}
